/* unplugin-vue-components disabled */// import * as firebaseui from "firebaseui";
import "firebaseui/dist/firebaseui.css";
import { FirebaseAuthentication } from "@capacitor-firebase/authentication";
import { GoogleAuthProvider, signInWithCredential, OAuthProvider } from "firebase/auth";
import { getFirebaseAuth } from "@/lib/firebase";
import { showLoading, closeLoading } from "@/hooks/useLoading";
import { isPlatform } from "@ionic/core";
export default defineComponent({
  props: ["successUrl"],
  setup(props) {
    const signInWithApple = async () => {
      await showLoading();
      try {
        const result = await FirebaseAuthentication.signInWithApple();
        const provider = new OAuthProvider("apple.com");
        const credential = provider.credential({
          accessToken: result.credential?.accessToken,
          idToken: result.credential?.idToken,
          rawNonce: result.credential?.nonce
        });
        const auth = getFirebaseAuth();
        await signInWithCredential(auth, credential);
        await closeLoading();
        return result.user;
      } catch (err) {
        await closeLoading();
        console.log(err);
      }
    };
    const signInWithGoogle = async () => {
      await showLoading();
      try {
        const result = await FirebaseAuthentication.signInWithGoogle();
        const credential = GoogleAuthProvider.credential(result.credential?.idToken);
        const auth = getFirebaseAuth();
        await signInWithCredential(auth, credential);
        await closeLoading();
        return result.user;
      } catch (err) {
        await closeLoading();
        console.log(err);
      }
    };
    return {
      signInWithGoogle,
      signInWithApple,
      isPlatform
    };
  }
});